  width:100%
} */



.MQTTDisplay {
  margin: 0 auto;
  max-width: 50em;
  border-style: solid;
  text-align: left;
  padding: 1em;
}

.MQTTSubscription {
  margin: 0 auto;
  border-style: solid;
  border-width: 1px;
  text-align: left;
  padding: 1em;
}
.MQTTMessage {
  font-size: 0.8em;
}

.AuthDisplay {
  margin: 0 auto;
  max-width: 50em;
  border-style: solid;
  text-align: left;
  padding: 1em;
}
.AuthData {
  border-style: solid;
  border-width: 1px;
}

.thin-border {
  border-style: solid;
  border-width: 1px;
}

.deviceList{
  display: flex;
  flex-direction: column;
}

.deviceCard {
  display: flex;
  justify-content: space-around;
  padding: 5px;
}

.deviceTitle {
  font-weight: bold;
}

.AudioPlayer {
  display: flex;
  justify-content: center;
}